import { shop } from "projectSettings"
import React from "react"
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import { MDXRenderer } from "gatsby-plugin-mdx"
import Layout from "globalLayoutsShopSinglePage"
import { Cart3 } from "react-bootstrap-icons"
import Row from "globalAtoms/Row"
import ElementList from "globalOrganisms/ElementList"
import BuyModal from "globalOrganisms/buyModal"
import * as helpers from "globalLibs/helpers"
import styled from "styled-components"

const PageStyle = styled.div`
  display: grid;
  .product-photo {
    display: flex;
    justify-content: center;
  }

  .product-about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 32px;
    margin-bottom: 64px;
    @media (max-width: 992px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .product-price_block {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    background-color: ${({ theme }) => theme.background_primary};
  }
`

export default function ShopProduct(props) {
  const node = props.data.mdx
  const frontmatter = props.data.mdx.frontmatter
  const extra = props.data.mdx.frontmatter.extra

  function getProductDescription() {
    if (node.excerpt !== null) return <MDXRenderer>{node.body}</MDXRenderer>
  }

  function getProductWarrantyText() {
    const categoryInfo = helpers.getCategoryInfo(frontmatter.categories[0])
    if (categoryInfo !== undefined && categoryInfo.warrantyText) {
      return <span>{categoryInfo.warrantyText}</span>
    } else {
      return <span>{shop.defaultProductWarrantyText}</span>
    }
  }

  function renderProductCharacteristics() {
    return (
      <>
        {extra.server !== null && (
          <Row className="element-medium">
            <span className="left-part px-16">Сервер</span>
            <span className="right-part px-16">{extra.server}</span>
          </Row>
        )}
        {extra.phone !== null && (
          <Row className="element-medium">
            <span className="left-part px-16">
              Привязка аккаунта и почты к телефону
            </span>
            <span className="right-part px-16">
              {extra.phone ? "Есть" : "Нет"}
            </span>
          </Row>
        )}
        {extra.mail !== null && (
          <Row className="element-medium">
            <span className="left-part px-16">Привязка аккаунта к почте</span>
            <span className="right-part px-16">
              {extra.mail ? "Есть" : "Нет"}
            </span>
          </Row>
        )}
        {extra.vehicles.x !== null && (
          <Row className="element-medium">
            <span className="left-part px-16">ТОПы</span>
            {extra.vehicles.x.map(element => (
              <span key={element}>
                <a
                  target="blank"
                  href={`https://wiki.wargaming.net/ru/index.php?search=${element}`}
                >
                  {element}
                </a>
              </span>
            ))}
          </Row>
        )}
        {extra.vehicles.premium !== null && (
          <Row className="element-medium">
            <span className="left-part px-16">Премиум техника</span>
            <span className="right-part px-16">
              {extra.vehicles.premium.map(element => (
                <a
                  key={element}
                  target="blank"
                  href={`https://wiki.wargaming.net/ru/index.php?search=${element}`}
                  className="theme-text-tint-blue"
                >
                  {element}
                </a>
              ))}
            </span>
          </Row>
        )}
      </>
    )
  }

  return (
    <>
      <GatsbySeo
        title={`Купить ${frontmatter.title} | Магазин World of Tanks`}
        description={node.excerpt}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: `Купить ${frontmatter.title} | Магазин World of Tanks`,
          description: node.excerpt,
        }}
      />
      <Layout>
        <PageStyle
          as="article"
          className="container-fluid mt-48"
          style={{ maxWidth: "1200px" }}
        >
          <div className="product-tab-main">
            <div className="product-about">
              <div className="left-part">
                <div className="product-photo">
                  <GatsbyImage
                    alt={frontmatter.title}
                    title={frontmatter.title}
                    image={getImage(frontmatter.cover)}
                    style={{ maxWidth: "600px", width: "100%" }}
                  />
                </div>
              </div>
              <div className="right-part">
                <h1 className="px-16 px-sm-0">{frontmatter.title}</h1>
                <div className="product-main-info">
                  <div className="product-price_block p-16">
                    <span className="large-h1 me-24">{`${extra.price} ₽`}</span>
                    <button
                      aria-label="product-buy-button"
                      data-bs-toggle="modal"
                      data-bs-target={`#${node.id.replace(
                        /[.,"*+!-?^${}()|[\]\\/]/gu,
                        ""
                      )}`}
                      className="element-large element-button green product-buy-button"
                    >
                      <i>
                        <Cart3 size={20} />
                      </i>
                      Купить
                    </button>
                    <BuyModal data={node}></BuyModal>
                  </div>
                  <ElementList
                    title="Доставка"
                    listClassName="large-subhead theme-shadow-border-inset-top p-16"
                  >
                    После оплаты товар будет отправлен на почту, указанную при
                    оформлении заказа
                  </ElementList>
                  <ElementList
                    title="Оплата"
                    listClassName="large-subhead theme-shadow-border-inset-top p-16"
                  >
                    Банковская карта или Qiwi
                  </ElementList>
                  <ElementList
                    title="Гарантия"
                    listClassName="large-subhead theme-shadow-border-inset-top p-16"
                  >
                    {getProductWarrantyText()}
                  </ElementList>
                </div>
              </div>
            </div>
            <div className="product-about">
              <div className="left-part">
                <h2 className="mb-16 px-16 px-sm-0">
                  Описание
                  <span className="theme-text-secondary">{` ${frontmatter.title}`}</span>
                </h2>
                <div className="theme-text-tertiary px-16 px-sm-0">
                  {getProductDescription()}
                </div>
              </div>
              <div className="right-part">
                <h2 className="mb-16 px-16 px-sm-0">
                  Характеристики
                  <span className="theme-text-secondary">{` ${frontmatter.title}`}</span>
                </h2>
                <div className="theme-text-tertiary">
                  <ElementList>{renderProductCharacteristics()}</ElementList>
                </div>
              </div>
            </div>
          </div>
        </PageStyle>
      </Layout>
    </>
  )
}

export const query = graphql`
  query($slug: String!) {
    mdx(slug: { eq: $slug }) {
      ...ShopSinglePage
    }
  }
`
